.nav{position:fixed;top:0;left:0;right:0;z-index:1000;padding:1rem 0;transition:all .3s ease}.scrolled{background:hsla(0,0%,100%,.95);backdrop-filter:blur(10px);box-shadow:0 2px 10px rgba(0,0,0,.1);padding:.5rem 0}.container{max-width:1200px;margin:0 auto;padding:0 2rem;display:flex;justify-content:space-between;align-items:center}.logo{display:flex;align-items:center;gap:.5rem;font-size:1.5rem;font-weight:700;color:#007bff;text-decoration:none;transition:color .3s ease}.logo:hover{color:#0056b3}.navLinks{display:flex;align-items:center;gap:2rem}.navLink{color:#333;text-decoration:none;font-weight:500;transition:color .3s ease;position:relative}.navLink::after{content:"";position:absolute;bottom:-5px;left:0;width:0;height:2px;background:#007bff;transition:width .3s ease}.navLink:hover::after,.navLink.active::after{width:100%}.authButton{padding:.5rem 1.5rem;background:#007bff;color:#fff;border:none;border-radius:50px;font-weight:600;text-decoration:none;transition:all .3s ease}.authButton:hover{background:#0056b3;transform:translateY(-2px)}.mobileMenuButton{display:none;background:none;border:none;cursor:pointer;padding:.5rem}.menuIcon{display:block;width:25px;height:2px;background:#333;position:relative;transition:all .3s ease}.menuIcon::before,.menuIcon::after{content:"";position:absolute;width:25px;height:2px;background:#333;transition:all .3s ease}.menuIcon::before{top:-8px}.menuIcon::after{bottom:-8px}@media(max-width: 768px){.mobileMenuButton{display:block}.navLinks{position:fixed;top:70px;left:0;right:0;background:#fff;padding:2rem;flex-direction:column;align-items:center;gap:1.5rem;box-shadow:0 2px 10px rgba(0,0,0,.1)}.scrolled .navLinks{top:60px}}